function generate() {
    return `    
    <div class="kc-select-cert-wrapper">
        <!-- select cert -->
        <div class="kc-select-cert">
            <div class="kc-select-cert__header"><span>인증서 선택</span></div>
            <div class="kc-select-cert__banner">
                <img src="${webnpki_config.path}/assets/banner/default_banner.png" alt="서비스 제공자 배너">
            </div>
            <div class="kc-select-cert__content">
                <button class="kc-button ml-auto" id="kc-open-cert-guide">
                    <span class="kc-icon-question"></span>
                    <span>브라우저 인증서란?</span>
                </button>
                <span class="kc-select-cert__content__label">인증서 목록</span>
                <div class="kc-select-cert__list" id="kc-select-cert-list">
                    <div class="kc-select-cert__list__dropzone d-none">
                        <div class="kc-icon-dropzone"></div>
                        <span>인증서 파일 묶음을 끌어다 놓으면</span>
                        <span>인증서 저장 및 서명이 가능합니다.</span>
                        <span>(*.pfx, *.p12 또는 signCert.der/signPri.key 묶음)</span>
                    </div>
                    <table class="kc-select-cert__list__table">
                        <thead>
                            <tr>
                                <th scope="col" style="width: 81px;"><span>구분</span></th>
                                <th scope="col" style="width: 82px;"><span>사용자</span></th>
                                <th scope="col" style="width: 82px;"><span>만료일</span></th>
                                <th scope="col" style="width: 82px;"><span>발급자</span></th>
                            </tr>
                        </thead>
                        <tbody id="kc-cert-list">                            
                        </tbody>
                    </table>
                </div>
                <div class="kc-select-cert__content__buttons">
                    <button class="kc-button ml-0" id="kc-export-cert">
                        <span>인증서 내보내기</span>
                    </button>
                    <button class="kc-button" id="kc-import-cert">
                        <span>인증서 가져오기</span>
                    </button>
                    <button class="kc-button ml-auto mr-0" id="kc-delete-cert">
                        <span class="kc-icon-delete-cert"></span>
                        <span>인증서 삭제하기</span>
                    </button>
                </div>
                <label class="kc-select-cert__content__label mt-0" for="kc-select-cert-qaqg">인증서 비밀번호</label>
                <div class="kc-select-cert__qaqg" onkeydown="return event.key != 'Enter';" autocomplete="off">
                    <input style="display:none" aria-hidden="true">
                    <input type="password" style="display:none" aria-hidden="true">
                    <input class="kc-select-cert__qaqg__input" type="password" id="kc-select-cert-qaqg"
                        placeholder="인증서 비밀번호는 대소문자를 구분합니다." autocomplete="off">
                    <button class="kc-icon-vkeyboard" data-target="kc-select-cert-qaqg"></button>
                </div>
                <div class="kc-select-cert__content__footer">
                    <button class="kc-button--confirm" id="kc-confirm-cert-selection">확인</button>
                    <button class="kc-button--cancel" id="kc-cancel-cert-selection">취소</button>
                </div>
            </div>
        </div>
        <!-- cert guide -->
        <div class="kc-cert-guide d-none">
            <div class="kc-cert-guide__header">
                <button class="kc-icon-close" id="kc-close-cert-guide"></button>
                <span class="kc-cert-guide__header__title">브라우저 인증서 이용 안내</span>
                <div class="kc-cert-guide__header__sub">
                    <span class="kc-icon-browser"></span>
                    <span>브라우저 인증서는 접속한 웹 사이트의 <br>브라우저 저장소에 저장된 인증서입니다.</span>
                </div>
            </div>
            <div class="kc-cert-guide__content">
                <span class="kc-cert-guide__content__title">브라우저에 인증서 저장하는 방법</span>
                <div class="kc-cert-guide__step">
                    <div class="kc-cert-guide__step__wrapper">
                        <span class="kc-badge">1</span>
                        <span class="kc-cert-guide__step__text">인증서 파일<sup>1&nbsp;</sup>끌어오기</span>
                        <span class="kc-cert-guide__step__subtext">파일 탐색기에서 인증서 파일을 마우스로 끌어와 <br>인증서 목록 영역에
                            놓습니다.</span>
                    </div>
                    <div class="kc-cert-guide__step__wrapper">
                        <div class="kc-cert-guide__annotation">
                            <span class="kc-cert-guide__annotation__text">
                                <sup>1</sup><em>인증서 파일</em>이란?
                            </span>
                            <div class="kc-cert-guide__annotation__content">
                                <div class="kc-cert-guide__annotation__content__bunch">
                                    <span class="kc-cert-guide__annotation__content__text">인증서 파일 묶음</span>
                                    <div class="kc-cert-guide__annotation__content__bunch__icons">
                                        <div class="kc-cert-guide__annotation__content__bunch__cert">
                                            <span class="kc-icon-bunch-cert"></span>
                                            <em>*.der</em>
                                        </div>
                                        <div class="kc-cert-guide__annotation__content__bunch__key">
                                            <span class="kc-icon-bunch-key"></span>
                                            <em>*.key</em>
                                        </div>
                                    </div>
                                    <span class="kc-cert-guide__annotation__content__text">파일 확장자 중요</span>
                                </div>
                                <div class="kc-cert-guide__annotation__content__pfx">
                                    <span class="kc-cert-guide__annotation__content__text">인증서 보관 파일</span>
                                    <span class="kc-icon-pfx"></span>
                                    <span class="kc-cert-guide__annotation__content__text">파일
                                        확장자</span><span><em>.pfx</em> 또는 <em>.p12</em></span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="kc-cert-guide__step__wrapper">
                        <span class="kc-badge">2</span>
                        <span class="kc-cert-guide__step__text">[인증서 가져오기]으로 가져오기</span>
                        <span class="kc-cert-guide__step__subtext">[인증서 가져오기] 버튼을 눌러 안내에 따라 <br>[인증서 찾기] 또는 [인증서
                            가져오기]를 선택합니다.</span>
                    </div>
                    <div class="kc-cert-guide__step__wrapper">
                        <span class="kc-cert-guide__step__subtext--comment">인증서 보관 파일의 경우 인증서 비밀번호를 필요로
                            합니다.</span>
                    </div>
                </div>
                <hr>
                <div class="kc-cert-guide__detail">
                    <div class="kc-cert-guide__detail__wrapper">
                        <span class="kc-badge">!</span>
                        <span class="kc-cert-guide__detail__text">
                            더 자세한 내용은 <button id="kc-open-detail-cert-guide">여기</button>를 클릭하세요.
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!-- import cert -->
    <div class="kc-import-cert-dim d-none">
        <div class="kc-import-cert draggable">
            <button class="kc-icon-close" id="kc-close-import-cert"></button>
            <div class="kc-import-cert__content">
                <div class="kc-import-cert__content__row">
                    <button class="kc-import-cert__content__button"
                        id="kc-open-file-explorer">인증서<br>찾기</button>
                    <span class="kc-import-cert__content__title"> 인증서 파일로 </span>
                    <span class="kc-import-cert__content__title"> 가져오기 </span>
                    <span class="kc-import-cert__content__text"> PC에 저장된 인증서 파일을 가져옵니다. 인증서 파일 위치를 모를 경우
                        <button id="kc-download-pfx-converter">
                            <span class="kc-icon-down"></span>
                            <span>인증서 변환 프로그램</span>
                        </button>을 <br>이용해주세요.
                    </span>
                </div>
                <hr class="kc-import-cert__content__divider">
                <div class="kc-import-cert__content__row">
                    <button class="kc-import-cert__content__button"
                        id="kc-open-get-auth-code">인증서<br>가져오기</button>
                    <span class="kc-import-cert__content__title"> 다른 곳에서 </span>
                    <span class="kc-import-cert__content__title"> 인증서 가져오기 </span>
                    <span class="kc-import-cert__content__text"> 다른 브라우저, <br>
                        다른 PC(<a target="_blank" rel="noopener noreferrer" href="#"
                            aria-label="">signgate.com</a>),
                        <br>
                        스마트폰(<a target="_blank" rel="noopener noreferrer" href="#" aria-label="">KICASing+</a>
                        앱)에 <br>저장된
                        인증서를
                        가져옵니다</span>
                </div>
                <hr class="kc-import-cert__content__divider2">
                <div class="kc-import-cert__content__row">
                    <div class="kc-import-cert__content__note">
                        <span class="kc-badge">!</span>
                        더 자세한 내용은 <button>여기</button>를 클릭하세요.
                    </div>
                </div>
            </div>
        </div>

        <div class="kc-get-auth-code draggable d-none">
            <div class="kc-get-auth-code__header"><span>인증번호 확인</span></div>
            <div class="kc-get-auth-code__content">
                <div class="kc-get-auth-code__wrapper">
                    <input type="text" class="kc-get-auth-code__number" id="kc-auth-code1" readonly
                        value="1234">
                    <span>-</span>
                    <input type="text" class="kc-get-auth-code__number" id="kc-auth-code2" readonly
                        value="5678">
                    <span>-</span>
                    <input type="text" class="kc-get-auth-code__number" id="kc-auth-code3" readonly
                        value="9123">
                </div>
                <div class="kc-get-auth-code__timer">
                    <span class="kc-get-auth-code__timer__time">5분 00초</span>
                    <button id="kc-refresh-auth-code"><span>인증번호 다시 받기</span>
                        <span class="kc-icon-refresh"></span>
                    </button>
                </div>
                <div class="kc-get-auth-code__guide">
                    <div class="kc-get-auth-code__guide__step">
                        <span class="kc-badge">1</span>
                        <span class="kc-get-auth-code__guide__text">인증번호 12자리를 확인합니다.</span>
                    </div>
                    <div class="kc-get-auth-code__guide__step">
                        <span class="kc-badge">2</span>
                        <span class="kc-get-auth-code__guide__text">다른 곳에서 인증서 내보내기를 진행합니다.</span>
                    </div>
                    <div class="kc-get-auth-code__guide__step">
                        <span class="kc-badge">3</span>
                        <span class="kc-get-auth-code__guide__text">다른 곳에서 인증번호 12자리를 입력한 후 인증서
                            <br>내보내기를 완료합니다.</span>
                    </div>
                    <div class="kc-get-auth-code__guide__step">
                        <span class="kc-badge">4</span>
                        <span class="kc-get-auth-code__guide__text">다음 버튼을 눌러 가져올 인증서 비밀번호를
                            입력합니다.</span>
                    </div>
                </div>
                <div class="kc-get-auth-code__footer">
                    <button class="kc-button--confirm" id="kc-get-remote-cert">다음</button>
                    <button class="kc-button--cancel" id="kc-cancel-get-auth-code">이전</button>
                </div>
            </div>
        </div>
        <input class="kc-import-cert__input" id="kc-import-cert-file" type="file" multiple
            accept=".p12, .pfx, .der, .key" title="To import certificate files.">
    </div>
    <!-- export cert -->
    <div class="kc-export-cert-dim d-none">
        <div class="kc-export-cert draggable">
            <button class="kc-icon-close" id="kc-close-export-cert"></button>
            <div class="kc-export-cert__content">
                <div class="kc-export-cert__content__row">
                    <button class="kc-export-cert__content__button"
                        id="kc-download-pfx-file">인증서<br>다운로드</button>
                    <span class="kc-export-cert__content__title"> 인증서 파일로 </span>
                    <span class="kc-export-cert__content__title"> 다운로드 </span>
                    <span class="kc-export-cert__content__text"> PC에 인증서 파일을 <br>다운로드합니다.</span>
                </div>
                <hr class="kc-export-cert__content__divider">
                <div class="kc-export-cert__content__row">
                    <button class="kc-export-cert__content__button"
                        id="kc-open-set-auth-code">인증서<br>내보내기</button>
                    <span class="kc-export-cert__content__title"> 다른 곳으로 </span>
                    <span class="kc-export-cert__content__title"> 인증서 내보내기 </span>
                    <span class="kc-export-cert__content__text"> 다른 브라우저, <br>다른 PC(
                        <a target="_blank" rel="noopener noreferrer" href="#" aria-label="">signgate.com</a>),
                        <br>스마트폰(<a target="_blank" rel="noopener noreferrer" href="#"
                            aria-label="">KICASing+</a>
                        앱)으로 <br>인증서를
                        내보냅니다.</span>
                </div>
                <hr class="kc-export-cert__content__divider2">
                <div class="kc-export-cert__content__row">
                    <div class="kc-export-cert__content__note">
                        <span class="kc-badge">!</span> 더 자세한 내용은 <button>여기</button>를 클릭하세요.
                    </div>
                </div>
            </div>
        </div>

        <div class="kc-set-auth-code draggable d-none">
            <div class="kc-set-auth-code__header">
                <span>인증서 내보내기</span>
            </div>
            <div class="kc-set-auth-code__content">
                <span class="kc-set-auth-code__label">인증번호 입력</span>
                <div class="kc-set-auth-code__wrapper">
                    <input type="text" class="kc-set-auth-code__number" id="kc-set-auth-code1" maxlength="4"
                        onkeyup="this.value=this.value.replace(/[^0-9]/g, '');"><span>-</span>
                    <input type="text" class="kc-set-auth-code__number" id="kc-set-auth-code2" maxlength="4"
                        onkeyup="this.value=this.value.replace(/[^0-9]/g, '');"><span>-</span>
                    <input type="text" class="kc-set-auth-code__number" id="kc-set-auth-code3" maxlength="4"
                        onkeyup="this.value=this.value.replace(/[^0-9]/g, '');">
                </div>
                <div class="kc-set-auth-code__guide">
                    <div class="kc-set-auth-code__guide__step">
                        <span class="kc-badge">1</span>
                        <span class="kc-set-auth-code__guide__text">다른 곳에서 인증서 가져오기를 통해 확인한
                            <br>인증번호 12자리를 입력합니다.</span>
                    </div>
                    <div class="kc-set-auth-code__guide__step">
                        <span class="kc-badge">2</span>
                        <span class="kc-set-auth-code__guide__text">확인 버튼을 눌러 인증서 내보내기를
                            완료합니다.</span>
                    </div>
                </div>
                <div class="kc-set-auth-code__footer">
                    <button class="kc-button--confirm" id="kc-complete-set-cert">확인</button>
                    <button class="kc-button--cancel" id="kc-cancel-set-auth-cde">취소</button>
                </div>
            </div>
        </div>
    </div>
    <!-- ask qaqg -->
    <div class="kc-ask-qaqg-dim d-none">
        <div class="kc-ask-qaqg draggable">
            <div class="kc-ask-qaqg__header">
                <span>인증서 비밀번호</span>
            </div>
            <div class="kc-ask-qaqg__content">
                <label for="kc-ask-qaqg">인증서 비밀번호를 입력하세요.</label>
                <div class="kc-ask-qaqg__wrapper" onkeydown="return event.key != 'Enter';" autocomplete="off">
                    <input style="display:none" aria-hidden="true">
                    <input type="password" style="display:none" aria-hidden="true">
                    <input class="kc-ask-qaqg__input" type="password" id="kc-ask-qaqg" autocomplete="off">
                    <button class="kc-icon-vkeyboard" data-target="kc-ask-qaqgh"></button>
                </div>
            </div>
            <div class="kc-ask-qaqg__footer">
                <button class="kc-button--confirm" id="kc-confirm-enter-password">확인</button>
                <button class="kc-button--cancel" id="kc-cancel-enter-password">취소</button>
            </div>
        </div>
    </div>`;
}

module.exports = {
    generate: generate,
};
