// 공통 기능
import _alert from "../alert";
import _logger from "../logger";
var _ = require("../common/utils");
var _relay = require("../common/relay");
var _keyboard = require("../common/keyboard");

var _askPwd = require("./ask-pwd");
var _box = require("./box");

// 외부에 노출될 객체
export default {
    init: () => {
        // create secukit-wrapper div and append to the body element.
        //_createElement();

        // set button events
        _setEvents();

        // enable _keyboard security
        if (webnpki_config._keyboardSecurity !== "") _keyboard.enableKeybaord($("#kcm-cert-qaqg"));
    },

    // show main dialog
    open: () => {
        _showCertList().then(() => {
            $("#secukit-container").removeClass("d-none");
        });
    },

    // close main dialog
    close: () => {
        $("#secukit-container").addClass("d-none");
        _askPwd.close();
    },

    // select cert event
    selectCert: (element) => {
        $(".kcm-select-cert__list__item").removeClass("active");
        $(element).addClass("active");
        $("#kcm-confirm-cert-selection").removeAttr("disabled");
    },

    getSelectedCert: () => {
        return $(".kcm-select-cert__list__item.active").data("certificate");
    },

    getSelectedCertJSON: () => {
        return $(".kcm-select-cert__list__item.active").data("certificateJSON");
    },

    getQaqg: () => {
        return $("#kcm-cert-qaqg").val();
    },
};

function _showCertList() {
    return new Promise((resovle, reject) => {
        webnpki
            .getCertificateList()
            .then((certificateList) => {
                var certificateListJSON = _.setCertInfoJson(certificateList);
                var certificateListRoot = $("#kcm-cert-list");
                certificateListRoot.html("");

                if (certificateList.length === 0) {
                    $(".kcm-select-cert__list__empty").removeClass("d-none");
                    $(".kcm-select-cert__footer").addClass("d-none");
                } else {
                    $(".kcm-select-cert__list__empty").addClass("d-none");
                    $(".kcm-select-cert__footer").removeClass("d-none");

                    var date = new Date();

                    for (var i = 0; i < certificateListJSON.size; i++) {
                        var certificate = certificateListJSON[i + 1];
                        var certItem = $(`<div class="kcm-select-cert__list__item" onclick="KICA_DIALOG.selectCert(this)"></div>`);

                        //if (i === 0) certItem.addClass("active");

                        var tag;
                        if (new Date(certificate.validateTo) - date <= 0) {
                            tag = '<span class="kcm-cert-info__exp-warning">만료</span>';
                        } else if (new Date(certificate.validateTo) - date <= 2629743000) {
                            tag = '<span class="kcm-cert-info__exp-warning">만료예정</span>';
                        } else {
                            tag = '<span class="kcm-cert-info__exp-warning d-none"></span>';
                        }

                        certItem.html(
                            `<div class="kcm-cert-info">
                                <div class="kcm-icon-cert"></div>
                                <div class="kcm-cert-info__subject">
                                    <span class="kcm-cert-info__cn">${certificate.cn}</span>
                                    ${tag}
                                </div>
                                <div class="kcm-cert-info__row">
                                    <span class="kcm-cert-info__label">발급자</span>
                                    <span class="kcm-cert-info__text">${certificate.issuerToName}</span>
                                </div>
                                <div class="kcm-cert-info__row">
                                    <div class="kcm-cert-info__label">구 분</div>
                                    <span class="kcm-cert-info__text">${certificate.policyToName}</span>
                                </div>
                                <div class="kcm-cert-info__row">
                                    <span class="kcm-cert-info__label">만료일</span>
                                    <span class="kcm-cert-info__text">${certificate.validateTo}</span>
                                </div>
                            </div>
                        
                            <hr class="kcm-divider">
                            <div class="kcm-cert-buttons">
                                <button class="kcm-delete-cert">
                                    인증서 삭제하기<span class=" kcm-icon-delete-cert"></span>
                                </button>
                                <button class="kcm-open-export-cert">
                                    인증서 내보내기<span class="kcm-icon-export-cert"></span>
                                </button>
                            </div>`
                        );

                        certItem.data("certificate", certificateList[i]);
                        certItem.data("certificateJSON", certificate);

                        certificateListRoot.append(certItem);
                    }

                    $(".kcm-select-cert__list__item").removeClass("active");
                    $("#kcm-confirm-cert-selection").attr("disabled", true);
                }
                resovle();
            })
            .catch((e) => {
                _alert.error(e.cdoe, e.message);
            });
    });
}

function _closeImportCert() {
    $(".kcm-import-cert").addClass("d-none");
}

function _closeExportCert() {
    $(".kcm-export-cert").addClass("d-none");
}

function _setEvents() {
    $("#kcm-confirm-cert-selection").on("click", () => {
        var certificateJSON = $(".kcm-select-cert__list__item.active").data("certificateJSON");
        _askPwd.open(certificateJSON, () => {
            $("#kc-confirm-cert-selection").click();
        });
    });

    $("#kcm-close-select-cert").on("click", () => {
        $("#secukit-container").addClass("d-none");
    });

    $("#kcm-close-qaqg").on("click", () => {
        _askPwd.close();
    });

    $("#kcm-cert-qaqg").on("keyup", (event) => {
        if (event.which == 13 || event.keyCode == 13) {
            event.preventDefault();
            $("#kcm-cert-qaqg").blur();
            $("#kcm-enter-qaqg").click();
            return;
        }

        if ($(event.target).val()) {
            $("#kcm-enter-qaqg").removeAttr("disabled");
        } else {
            $("#kcm-enter-qaqg").attr("disabled", true);
        }
    });

    $(".kcm-open-import-cert").on("click", () => {
        $(".kcm-import-cert").removeClass("d-none");
    });

    $("#kcm-open-file-explorer").on("click", () => {
        $("#kcm-import-cert-file").click();
    });

    // open file from the file dialog.
    $("#kcm-import-cert-file").on("change", function (event) {
        event.preventDefault();
        var files = event.target.files;
        if (files.length === 0) {
            return;
        }

        var _certBuffer, _keyBuffer, _p12Buffer;

        // 파일 확장자에 따라 버퍼에 등록
        function onloadReader(file, callback) {
            return function (event) {
                var cName = file.name.toLowerCase();
                if (/\.der$/.test(cName)) {
                    _certBuffer = event.target.result;
                    callback(false);
                } else if (/\.key$/.test(cName)) {
                    _keyBuffer = event.target.result;
                    callback(false);
                } else if (/\.pfx$|\.p12$/.test(cName)) {
                    _p12Buffer = event.target.result;
                    callback(true);
                }
            };
        }

        function fileLoadCallback(p12) {
            if (p12) {
                _askPwd.open(undefined, () => {
                    var pwd = $("#kcm-cert-qaqg").val();

                    webnpki
                        .importPKCS12(_p12Buffer, pwd)
                        .then(function () {
                            _showCertList();
                            _askPwd.close();
                            _closeImportCert();
                        })
                        .catch(function (e) {
                            _alert.error(e.code, e.message, {
                                onAfterClose: () => {
                                    setTimeout(() => {
                                        $("#kcm-cert-qaqg").focus();
                                    }, 1);
                                },
                            });
                        });
                });
            }
            // 키쌍인 경우
            else {
                // Key랑 Cert 버퍼가 모두 있을 경우 추가
                if (!_certBuffer && !_keyBuffer) {
                    webnpki
                        .importCertAndKey(_certBuffer, _keyBuffer)
                        .then(function () {
                            _showCertList();
                            _closeImportCert();
                        })
                        .catch(function (e) {
                            _alert.error(e.code, e.message);
                        });
                }
            }
        }

        var i, reader;
        for (i = 0; i < files.length; i += 1) {
            reader = new FileReader();
            reader.readAsArrayBuffer(files[i]);
            reader.onload = onloadReader(files[i], fileLoadCallback);
        }

        if (event.target.value) {
            event.target.value = "";
        }
    });

    $("#kcm-open-get-auth-code").on("click", () => {
        _relay.getAuthCode((authCode, validTime) => {
            $(".kcm-get-auth-cde__input").val(authCode);
        });
    });

    $("#kcm-close-import-cert").on("click", () => {
        _closeImportCert();
    });

    $("#kcm-get-remote-cert").on("click", () => {
        _relay.getCert($(".kcm-get-auth-cde__input").val(), (base64P12) => {
            _askPwd.open(undefined, () => {
                var pwd = $("#kcm-cert-qaqg").val();
                var _temBuffer = webnpki.bin.decodeBase64(base64P12);

                webnpki
                    .importPKCS12(_temBuffer, pwd)
                    .then(function () {
                        _showCertList();
                        _askPwd.close();
                        _closeImportCert();
                    })
                    .catch(function (e) {
                        _alert.error(e.code, e.message, {
                            onAfterClose: () => {
                                setTimeout(() => {
                                    $("#kcm-cert-qaqg").focus();
                                }, 1);
                            },
                        });
                    });
            });
        });
    });

    $(document).on("click", ".kcm-delete-cert", (event) => {
        let certificate = $(event.target).parents(".kcm-select-cert__list__item.active").data("certificate");
        _alert.deleteConfirm().then((result) => {
            // if user click the confrim button
            if (result.value) {
                // delete certificate
                webnpki
                    .deleteCertificate(certificate)
                    .then(() => {
                        // if the certificate is already deleted, shows error message
                        // refresh the certificate list.
                        _showCertList();
                    })
                    .catch((e) => {
                        _alert.error("DELETE_CERT_FAILED", KICA_ERROR_MSG.get("DELETE_CERT_FAILED"));
                    });
            }
        });
    });

    $(document).on("click", ".kcm-open-export-cert", () => {
        let certificate = $(event.target).parents(".kcm-select-cert__list__item.active").data("certificate");
        let certificateJSON = $(event.target).parents(".kcm-select-cert__list__item.active").data("certificateJSON");

        _askPwd.open(certificateJSON, () => {
            var pwd = $("#kcm-cert-qaqg").val();
            webnpki
                .exportPKCS12(certificate, pwd)
                .then(function (result) {
                    // close ask password dialog
                    // 중계서버 내보내기 시, pkcs 1 서명을 진행해야하기 때문에 여기서 비밀번호 초기화 하지 않음
                    _askPwd.close();
                    // open export certificate dialog
                    _openExportCert(result, certificate, pwd);
                })
                .catch(function (e) {
                    // 오류 났을 때, 오류 메시지 출력해주고
                    _alert.error(e.code, e.message, {
                        onAfterClose: () => {
                            setTimeout(() => {
                                $("#kcm-cert-qaqg").focus();
                            }, 1);
                        },
                    });
                });
        });
    });

    $("#kcm-close-export-cert").on("click", () => {
        _closeExportCert();
    });
}

function _openExportCert(p12, certificate, pwd) {
    $("#kcm-download-pfx-file").unbind("click");
    $("#kcm-download-pfx-file").on("click", () => {
        var binPFX = webnpki.bin.decodeBase64(p12);
        var blob = new Blob([binPFX], { type: "application/octet-stream" });
        _.downloadPFX(blob, certificate.getSubjectDN() + ".pfx");
    });

    $("#kcm-complete-set-cert").unbind("click");
    $("#kcm-complete-set-cert").on("click", () => {
        var authCode = $(".kcm-set-auth-code__input").val();
        var options = {
            digestAlg: "sha256",
            charset: webnpki_config.charset,
            outputEncoding: "base64",
        };
        webnpki
            .signPkcs1(certificate, pwd, p12, options)
            .then(function (res) {
                _relay.setCert(authCode, p12, res, certificate, function () {
                    _closeExportCert();
                });
            })
            .catch(function (e) {
                _alert.error(e.code, e.message);
            });
    });

    $(".kcm-export-cert").removeClass("d-none");
}
