function open(confirm, cancel) {
    $("#kc-ask-qaqg").val("");
    if (webnpki_config.keyboardSecurity === "default") {
        webnpki.setPassword();
    }
    $("#kc-ask-qaqg").focus();

    $("#kc-confirm-enter-password").unbind("click");
    $("#kc-confirm-enter-password").click(confirm);

    $("#kc-cancel-enter-password").unbind("click");
    $("#kc-cancel-enter-password").click(cancel);

    $(".kc-ask-qaqg-dim").removeClass("d-none");
}

module.exports = {
    open: open,
};
