import "weakmap-polyfill";
import "./scss/main.scss";
import "./vendor/jquery/jquery.keyboard";
import "./vendor/jquery/colResizable-1.6";

import _alert from "./js/alert";
import _logger from "./js/logger";
import _pc from "./js/pc/main";
import _mobile from "./js/mobile/main";

var _i18n = require("./lang/i18n");

(function () {
    // an object to provide public funciton
    function _getUI() {
        var vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
        if (vw > 576) {
            return _pc;
        } else {
            return _mobile;
        }
    }

    window.KICA_ALERT = _alert;
    window.KICA_DIALOG = {
        openSelectCertificateDialog: function () {
            _getUI().open();
        },
        closeSelectCertificateDialog: function () {
            _getUI().close();
        },
        selectCert: function (param) {
            _getUI().selectCert(param);
        },

        getSelectedCert: function () {
            return _getUI().getSelectedCert();
        },

        getSelectedCertJSON: function () {
            return _getUI().getSelectedCertJSON();
        },

        getQaqg: function () {
            return _getUI().getQaqg();
        },
    };

    // load language file first for drawing UI.
    _i18n.setLanguage(webnpki_config.language, function (error) {
        if (error) {
            _logger.error(error);
            _alert.error("LANGUAGE_NOT_SUPPORTED", error, { footer: "Supported language codes are [ <b> KO, EN, and FR</b>]." });
            return;
        }
        $('<div id="secukit-container" class="d-none"></div>').appendTo("body");
        _pc.init(); // _pc UI 초기화
        _mobile.init(); // _mobile UI 초기화
    });
})();
