function open(certificate, confirm) {
    _initPassword();

    if (certificate) {
        $(".kcm-qaqg__cert").removeClass("d-none");
        var date = new Date();
        var tag;
        if (new Date(certificate.validateTo) - date <= 0) {
            tag = '<span class="kcm-qaqg__cert__info__exp-warning">만료</span>';
        } else if (new Date(certificate.validateTo) - date <= 2629743000) {
            tag = '<span class="kcm-qaqg__cert__info__exp-warning">만료예정</span>';
        } else {
            tag = '<span class="kcm-qaqg__cert__info__exp-warning d-none"></span>';
        }

        $("#kcm-qaqg-cert-subject").html(certificate.cn + tag);
        $("#kcm-qaqg-cert-issuer").html(certificate.issuerToName);
        $("#kcm-qaqg-cert-policy").html(certificate.policyToName);
        $("#kcm-qaqg-cert-exp").html(certificate.validateTo);
    } else {
        $(".kcm-qaqg__cert").addClass("d-none");
    }

    $("#kcm-enter-qaqg").unbind("click");
    $("#kcm-enter-qaqg").on("click", confirm);

    $(".kcm-qaqg-dim").removeClass("d-none");
}

function close() {
    $(".kcm-qaqg-dim").addClass("d-none");
}

function _initPassword() {
    // if keybaord security is applied, reset inner password
    if (webnpki_config.keyboardSecurity === "default") {
        webnpki.setPassword();
    }

    $("#kcm-cert-qaqg").val("");
    $("#kcm-enter-qaqg").attr("disabled", true);

    setTimeout(() => {
        $("#kcm-cert-qaqg").focus();
    }, 1);
}

module.exports = {
    init: _initPassword,
    open: open,
    close: close,
};
