// 공통 기능
import _alert from "../alert";
import _logger from "../logger";
var _ = require("../common/utils");
var relay = require("../common/relay");
var keyboard = require("../common/keyboard");

// UI 관련
var askPwd = require("./ask-pwd");
var box = require("./box");
var v_keyboard = require("./virtual-keyboard");

// 인증서 추가 시 필요한 내부 버퍼 변수
var _p12Buffer, _certBuffer, _keyBuffer;

// 외부에 노출될 객체
export default {
    init: function () {
        _createElement();

        // set button events
        _setEvents();

        // enable draggable option.
        $(".draggable").draggable();

        // enable keyboard security
        if (webnpki_config.keyboardSecurity !== "") {
            var qaqg = $("#kc-select-cert-qaqg, #kc-ask-qaqg, #kcm-cert-qaqg");
            keyboard.enableKeybaord(qaqg);
        }
    },

    // show main dialog
    open: function () {
        _initPassword();
        _showCertList().then(() => {
            $("#secukit-container").removeClass("d-none");
        });
    },

    // close main dialog
    close: function () {
        _close();
    },

    // select cert event
    selectCert: function (index) {
        $("#kc-cert-list tr").removeClass("active");
        $(`#kc-cert-list tr[id=${index}]`).addClass("active");
    },

    getSelectedCert: function () {
        return _getSelectedCert();
    },

    getSelectedCertJSON: function () {
        return $("#kc-cert-list .active").data("certJSON");
    },

    getQaqg: function () {
        return $("#kc-select-cert-qaqg").val();
    },
};

function _getSelectedCert() {
    return $("#kc-cert-list .active").data("cert");
}

function _createElement() {
    var wrapper = $(`<div class="kc-wrapper" lang="${webnpki_config.language.toLocaleLowerCase()}"></div>`).appendTo("#secukit-container");
    wrapper.html(box.generate());
}

function _close() {
    $("#secukit-container").addClass("d-none");

    // destroy virtual keyboard
    v_keyboard.destroy();
}

// close browser guide common function
function _closeCertGuide() {
    $(".kc-cert-guide").addClass("d-none");
    $(".kc-select-cert-wrapper").removeClass("guide-opened");
}

function _closeAskQaqg() {
    v_keyboard.destroy();
    $(".kc-ask-qaqg-dim").addClass("d-none");
}

function _closeImportCert() {
    _initPassword();
    $(".kc-import-cert-dim, kc-import-cert, .kc-get-auth-code").addClass("d-none");
}

function _closeExportCert() {
    _initPassword();
    $(".kc-export-cert-dim, .kc-export-cert, .kc-set-auth-code").addClass("d-none");
}

function _clearCertBuffer() {
    _certBuffer = undefined;
    _keyBuffer = undefined;
    _p12Buffer = undefined;
}

function _showCertList() {
    return new Promise((resolve, reject) => {
        webnpki
            .getCertificateList()
            .then((certificateList) => {
                var certificateListJSON = _.setCertInfoJson(certificateList);
                var certificateListRoot = $("#kc-cert-list");
                certificateListRoot.html("");
                
                if (certificateList.length === 0) {
                    $("tbody").children().remove();
                    $(".kc-select-cert__list__dropzone").removeClass("d-none");
                } else {
                    $(".kc-select-cert__list__dropzone").addClass("d-none");

                    for (var i = 0; i < certificateListJSON.size; i++) {
                        var certificate = certificateListJSON[i + 1];

                        var rowClass = "";
                        if (i == 0) rowClass = 'class="active"';

                        var index = i + 1;
                        var row = $(`<tr ${rowClass} id="${index}" onclick="KICA_DIALOG.selectCert(${index})">`);

                        var certIcon = "kc-icon-cert";
                        if (new Date(certificate.todate) - new Date() <= 2629743000) {
                            certIcon = "kc-icon-exp-cert";
                        }

                        var html = `<td scope="col"><span><span class="${certIcon}"></span>${certificate.usageToName}</span></td>
                <td scope="col"><span>${certificate.cn}</span></td>
                <td scope="col"><span style="text-align: center;">${certificate.todate}</span></td>
                <td scope="col"><span>${certificate.issuerToName}</span></td>`;

                        row.html(html);
                        row.data("cert", certificateList[i]);
                        row.data("certJSON", certificate);

                        certificateListRoot.append(row);
                    }

                    $(".kc-select-cert__list__table").colResizable({ liveDrag: true });
                }
                resolve();
            })
            .catch((error) => {
                _alert.error(error.cdoe, error.message);
                reject();
            });
    });
}

function _setEvents() {
    // button event for cancel on the select certificate dialog.
    $("#kc-cancel-cert-selection").on("click", function (event) {
        _close();
    });

    // browser Guide Toggle Button
    $("#kc-open-cert-guide").on("click", function (event) {
        // if browser guide is not opened
        if ($(".kc-cert-guide").hasClass("d-none")) {
            $(".kc-cert-guide").removeClass("d-none");
            $(".kc-select-cert-wrapper").addClass("guide-opened");
        } else {
            // else browser guide is opened
            _closeCertGuide();
        }
    });

    // button event for 'x' on the browser guide
    $("#kc-close-cert-guide").on("click", function (event) {
        _closeCertGuide();
    });

    // button event for detail link on the browser guide
    $("#kc-open-detail-cert-guide").on("click", function (event) {
        _.download(webnpki_config.path + "assets/guide/browser_certificate_guide.pdf", "Browser Certificate Detail Guide.pdf");
    });

    // even handler for drag & drop certificate files.
    function _onDragOverCert() {
        event.preventDefault();
        if (typeof event.target.classList !== "undefined") {
            event.target.classList.add("dragover");
        }
    }

    function _onDragLeaveCert() {
        event.preventDefault();
        if (typeof event.target.classList !== "undefined") {
            event.target.classList.remove("dragover");
        }
    }

    function _onDropCert(event) {
        event.preventDefault();
        event.target.classList.remove("dragover");
        if (!event.dataTransfer || event.dataTransfer.files.length == 0) {
            return;
        }
        _selectCertFile(event.dataTransfer);
    }
    var certList = document.getElementById("kc-select-cert-list");
    certList.addEventListener("dragover", _onDragOverCert, false);
    certList.addEventListener("dragleave", _onDragLeaveCert, false);
    certList.addEventListener("drop", _onDropCert, false);

    // toggle button to open virtaul keyboards
    // the element could be 'select-cert-qaqg' or 'ask-qaqg'
    $(".kc-icon-vkeyboard").on("click", function (event) {
        var element = $(this);

        // get the target of vritual keyboard.
        var input = $("#" + element.attr("data-target"));

        // init password
        _initPassword();

        var vk = input.getkeyboard();

        // if virtual keybaord is not opened.
        if (typeof vk === "undefined") {
            element.addClass("active");
            v_keyboard.init(input);
        }
        // else virtual keyboard is opened.
        else {
            element.removeClass("active");
            vk.destroy();
        }
    });

    // to close the virtual keyboard when an user click the outside of virtual keyboard and input element.
    $("#kc-select-cert-qaqg #kc-ask-qaqg").on("blur", function (event) {
        $(this).getkeyboard().close();
    });

    // button event for delete certificate
    $("#kc-delete-cert").on("click", function (event) {
        var certificate = _getSelectedCert();
        if (!certificate) {
            _alert.error("SELECT_CERT", KICA_ERROR_MSG.get("SELECT_CERT"));
            return;
        }

        // open confirm dialog for deleting certificate.
        _alert
            .deleteConfirm()
            // after button click
            .then(function (result) {
                // if user click the confrim button
                if (result.value) {
                    // delete certificate
                    webnpki.deleteCertificate(certificate).then(function (res, error) {
                        // if the certificate is already deleted, shows error message
                        if (error != undefined) _alert.error("DELETE_CERT_FAILED", KICA_ERROR_MSG.get("DELETE_CERT_FAILED"));

                        // refresh the certificate list.
                        _showCertList();
                    });
                }
            });
    });

    // open import certificate dialog
    $("#kc-import-cert").on("click", function (event) {
        $(".kc-import-cert-dim, .kc-import-cert").removeClass("d-none");
    });

    // button event for 'x' on the import certificate
    $("#kc-close-import-cert").on("click", function (event) {
        _closeImportCert();
    });

    // download convert pfx program
    $("#kc-download-pfx-converter").on("click", function (event) {
        if (_.getOS() === "Windows") {
            _.download(webnpki_config.path + "assets/plugin/SecuKitCertExport.exe", "SecuKitCertExport.exe");
        } else {
            _alert.error("IS_NOT_WINDOWS", KICA_ERROR_MSG.get("IS_NOT_WINDOWS"));
        }
    });

    // open file explorer
    $("#kc-open-file-explorer").on("click", function (event) {
        $("#kc-import-cert-file").click();
    });

    // open file from the file dialog.
    $("#kc-import-cert-file").on("change", function (event) {
        event.preventDefault();
        _selectCertFile(this);
    });

    // button export certificate
    $("#kc-export-cert").on("click", function (event) {
        var certificate = _getSelectedCert();
        if (!certificate) {
            _alert.error("SELECT_CERT", KICA_ERROR_MSG.get("SELECT_CERT"));
            return;
        }

        // confirm for ask password dialog
        var _confirm = function () {
            var pwd = $("#kc-ask-qaqg").val();
            if (pwd.length === 0) {
                _alert.error("EMPTY_PASSWORD", KICA_ERROR_MSG.get("EMPTY_PASSWORD"), {
                    onAfterClose: function () {
                        $("#kc-ask-qaqg").focus();
                    },
                });
                return;
            }

            // export pkcs12 data
            webnpki
                .exportPKCS12(certificate, pwd)
                .then(function (result) {
                    // close ask password dialog
                    // 중계서버 내보내기 시, pkcs 1 서명을 진행해야하기 때문에 여기서 비밀번호 초기화 하지 않음
                    _closeAskQaqg();
                    // open export certificate dialog
                    _openExportCert(result, certificate, pwd);
                })
                .catch(function (error) {
                    // 오류 났을 때, 오류 메시지 출력해주고
                    _alert.error(error.code, error.message);
                    _initPassword();
                    // 비밀번호 다시 입력할 수 있도록 포커싱
                    $("#kc-ask-qaqg").focus();
                });
        };

        var _cancel = function () {
            _closeAskQaqg();
            _initPassword();
            return false;
        };

        askPwd.open(_confirm, _cancel);
    });

    // button event for 'x' on the export certificate
    $("#kc-close-export-cert").on("click", function (event) {
        _closeExportCert();
    });

    // open get auth code dialog
    $("#kc-open-get-auth-code").on("click", function (event) {
        relay.getAuthCode(_openGetAuthCode);
    });

    // refresh the auth code.
    $("#kc-refresh-auth-code").on("click", function (event) {
        relay.getAuthCode(_openGetAuthCode);
    });

    // back button for get auth code dialog
    $("#kc-cancel-get-auth-code").on("click", function () {
        // clear the auth code timer
        if (timer) clearInterval(timer);

        $(".kc-import-cert").removeClass("d-none");
        $(".kc-get-auth-code").addClass("d-none");
    });

    // open set auth code dialog
    $("#kc-cancel-set-auth-cde").on("click", function () {
        $(".kc-export-cert").removeClass("d-none");
        $(".kc-set-auth-code").addClass("d-none");
    });

    // auth code 입력 란에 숫자만 입력하도록 이벤트 처리
    $(".kc-set-auth-code__number").on("keypress", _.isNumber);
}

function _selectCertFile(target) {
    // 버퍼 초기화..
    _clearCertBuffer();
    var files = target.files;
    if (files.length === 0) {
        return;
    }

    // 파일 확장자에 따라 버퍼에 등록
    function onloadReader(file, callback) {
        return function (event) {
            var cName = file.name.toLowerCase();
            if (/\.der$/.test(cName)) {
                _certBuffer = event.target.result;
                callback(false);
            } else if (/\.key$/.test(cName)) {
                _keyBuffer = event.target.result;
                callback(false);
            } else if (/\.pfx$|\.p12$/.test(cName)) {
                _p12Buffer = event.target.result;
                callback(true);
            }
        };
    }

    function fileLoadCallback(p12) {
        // pfx 파일인 경우 비밀번호 묻는 팝업 필요
        if (p12) {
            var _confirm = function () {
                var pwd = $("#kc-ask-qaqg").val();
                if (pwd.length == 0) {
                    _alert.error("EMPTY_PASSWORD", KICA_ERROR_MSG.get("EMPTY_PASSWORD"), {
                        onAfterClose: function () {
                            $("#kc-ask-qaqg").focus();
                        },
                    });
                    return false;
                }

                webnpki
                    .importPKCS12(_p12Buffer, pwd)
                    .then(function () {
                        _showCertList();
                        _closeAskQaqg();
                        _closeImportCert();
                        _clearCertBuffer();
                        _initPassword();
                    })
                    .catch(function (e) {
                        _alert.error(e.code, e.message);
                        _initPassword();
                        $("#kc-ask-qaqg").focus();
                        return false;
                    });
            };

            var _cancel = function () {
                _closeAskQaqg();
                _clearCertBuffer();
                _initPassword();
                return false;
            };

            askPwd.open(_confirm, _cancel);
        }
        // 키쌍인 경우
        else {
            var crt, key;
            crt = typeof _certBuffer === "undefined" ? false : true;
            key = typeof _keyBuffer === "undefined" ? false : true;

            // Key랑 Cert 버퍼가 모두 있을 경우 추가
            if (crt && key) {
                webnpki
                    .importCertAndKey(_certBuffer, _keyBuffer)
                    .then(function () {
                        _showCertList();
                        _closeAskQaqg();
                        _closeImportCert();
                        _clearCertBuffer();
                    })
                    .catch(function (error) {
                        _alert.error(error.code, error.message);
                        _clearCertBuffer();
                    });
            }
        }
    }

    var i, reader;
    for (i = 0; i < files.length; i += 1) {
        reader = new FileReader();
        reader.readAsArrayBuffer(files[i]);
        reader.onload = onloadReader(files[i], fileLoadCallback);
    }

    if (target.value) {
        target.value = "";
    }
}

function _openExportCert(p12, certificate, pwd) {
    $(".kc-export-cert-dim, .kc-export-cert").removeClass("d-none");

    $("#kc-download-pfx-file").unbind("click");
    $("#kc-download-pfx-file").click(function () {
        var binPFX = webnpki.bin.decodeBase64(p12);
        // Download PFX
        var blob = new Blob([binPFX], { type: "" });
        _.downloadPFX(blob, certificate.getSubjectDN() + ".pfx");
    });

    // Open set auth code
    $("#kc-open-set-auth-code").unbind("click");
    $("#kc-open-set-auth-code").on("click", function () {
        // 인증서 내보개니 확인 버튼 이벤트 바인딩
        $("#kc-complete-set-cert").unbind("click");
        $("#kc-complete-set-cert").on("click", function () {
            var authCode = $("#kc-set-auth-code1").val() + $("#kc-set-auth-code2").val() + $("#kc-set-auth-code3").val();
            if (authCode.length !== 12) return;

            var options = {
                digestAlg: "sha256",
                charset: webnpki_config.charset,
                outputEncoding: "base64",
            };
            webnpki
                .signPkcs1(certificate, pwd, p12, options)
                .then(function (res) {
                    relay.setCert(authCode, p12, res, certificate, function () {
                        _closeExportCert();
                        _initPassword();
                    });
                })
                .catch(function (error) {
                    console.log(error);
                });
        });

        // 화면 보여주기
        $("#kc-set-auth-code1, #kc-set-auth-code2, #kc-set-auth-code3").val("");
        $(".kc-set-auth-code").removeClass("d-none");
        $(".kc-export-cert ").addClass("d-none");
    });
}

// auth code timer
var timer;

function _openGetAuthCode(authCode, validTime) {
    $("#kc-auth-code1").val(authCode.slice(0, 4));
    $("#kc-auth-code2").val(authCode.slice(4, 8));
    $("#kc-auth-code3").val(authCode.slice(8, 12));

    var minutes;
    var seconds;

    function _validTimer() {
        minutes = Math.floor((validTime % (1000 * 60 * 60)) / (1000 * 60));
        seconds = Math.floor((validTime % (1000 * 60)) / 1000);
        $(".kc-get-auth-code__timer__time").html(`${minutes}분 ${seconds}초`);

        validTime -= 1000;
    }

    if (timer) clearInterval(timer);

    // 타이머 시작
    _validTimer();
    timer = setInterval(function () {
        _validTimer();

        if (validTime < 0) {
            clearInterval(timer);
            $(".kc-get-auth-code__timer__time").html("만료됨");
        }
    }, 1000);

    $("#kc-get-remote-cert").unbind("click");
    $("#kc-get-remote-cert").click(function () {
        relay.getCert(authCode, function (base64P12) {
            var _confirm = function () {
                var pwd = $("#kc-ask-qaqg").val();
                if (pwd.length === 0) {
                    _alert.error("EMPTY_PASSWORD", KICA_ERROR_MSG.get("EMPTY_PASSWORD"), {
                        onAfterClose: function () {
                            $("#kc-ask-qaqg").focus();
                        },
                    });
                    return;
                }

                var _temBuffer = webnpki.bin.decodeBase64(base64P12);

                webnpki
                    .importPKCS12(_temBuffer, pwd)
                    .then(function () {
                        _showCertList();
                        _closeAskQaqg();
                        _clearCertBuffer();
                        _closeImportCert();
                        $("#kc-auth-code1, #kc-auth-code2, #kc-auth-code3").val("");
                        _initPassword();
                    })
                    .catch(function (ｅ) {
                        _alert.error(e.code, e.message);
                        _initPassword();
                        $("#kc-ask-qaqg").focus();
                        return false;
                    });
            };

            var _cancel = function () {
                _closeAskQaqg();
                _clearCertBuffer();
                _initPassword();
                return false;
            };

            askPwd.open(_confirm, _cancel);
        });
    });

    $(".kc-import-cert").addClass("d-none");
    $(".kc-get-auth-code").removeClass("d-none");
}

function _initPassword() {
    // if keybaord security is applied, reset inner password
    if (webnpki_config.keyboardSecurity === "default") {
        webnpki.setPassword();
    }

    $("#kc-select-cert-qaqg, #kc-ask-qaqg").val("");
}
