var Swal = require('sweetalert2')
var iconDelete = require('../images/icon/icon-recyclebin.png')

function KICA_ALERT() {
    var object = {};

    object.alert = function(title, text, options) {
        var alertOptions = {
            icon: 'info',
            title: title,
            text: text,
        }

        if (options !== undefined) {
            var keys = Object.keys(options);

            for (var i = 0; i < keys.length; i++) {
                var key = keys[i];
                alertOptions[key] = options[key];
            }
        }

        Swal.fire(alertOptions);
    }

    object.error = function(title, text, options) {
        if (webnpki_config.alert < 1) {
            return;
        }

        var alertOptions = {
            icon: 'error',
            title: title,
            text: text,
        }

        if (options !== undefined) {
            var keys = Object.keys(options);

            for (var i = 0; i < keys.length; i++) {
                var key = keys[i];
                alertOptions[key] = options[key];
            }
        }

        Swal.fire(alertOptions);
    }

    object.info = function(title, text, options) {
        if (webnpki_config.alert < 2) {
            return;
        }

        var alertOptions = {
            icon: 'info',
            title: title,
            text: text,
        }

        if (options !== undefined) {
            var keys = Object.keys(options);

            for (var i = 0; i < keys.length; i++) {
                var key = keys[i];
                alertOptions[key] = options[key];
            }
        }

        Swal.fire(alertOptions);
    }

    object.success = function(title, text, options) {
        if (webnpki_config.alert < 2) {
            return;
        }

        var alertOptions = {
            icon: 'success',
            title: title,
            text: text,
        }

        if (options !== undefined) {
            var keys = Object.keys(options);

            for (var i = 0; i < keys.length; i++) {
                var key = keys[i];
                alertOptions[key] = options[key];
            }
        }

        Swal.fire(alertOptions);
    }

    object.warn = function(title, text, options) {
        if (webnpki_config.alert < 3) {
            return;
        }

        var alertOptions = {
            icon: 'warning',
            title: title,
            text: text,
        }

        if (options !== undefined) {
            var keys = Object.keys(options);

            for (var i = 0; i < keys.length; i++) {
                var key = keys[i];
                alertOptions[key] = options[key];
            }
        }

        Swal.fire(alertOptions);
    }
    object.deleteConfirm = function() {
        var alertOptions = {
            title: KICA_CORPUS.DELETE_CERTIFICATE_QUESTION_TITLE,
            html: '<div class="swal2-icon swal2-warning swal2-icon-show" style="display: inline-flex;"><div class="swal2-icon-content">!</div></div>' + KICA_CORPUS.DELETE_CERTIFICATE_QUESTION_TEXT,
            imageUrl: iconDelete,            

            buttons: {
                confirm: {
                    value: true
                },
                cancel: {
                    value: false
                }

            },
            customClass: {
                confirmButton: 'confirm-button-class',
                cancelButton: 'cancel-button-class',

            },
            showCloseButton: true,
            showCancelButton: true,
            confirmButtonText: KICA_CORPUS.DELETE_CERTIFICATE_BUTTON_CONFIRM,
            cancelButtonText: KICA_CORPUS.DELETE_CERTIFICATE_BUTTON_CANCLE

        }

        return Swal.fire(alertOptions);

    }
    return object;
}

export default KICA_ALERT();